$lightGreen: #b9e7e7;

.app {
	display: flex;
	justify-content: center;
	.container {
		width: 1024px;
		// reset links
		.link {
			text-decoration: none;
			color: inherit;
		}
		// [ ]: Login and Register
		.auth {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			height: 100vh;
			background-color: $lightGreen;

			h1 {
				font-size: 20px;
				color: teal;
				margin-bottom: 20px;
			}

			form {
				display: flex;
				flex-direction: column;
				padding: 50px;
				background-color: white;
				width: 200px;
				gap: 20px;

				input {
					padding: 10px;
					border: none;
					border-bottom: 1px solid gray;
				}

				button {
					padding: 10px;
					border: none;
					background-color: teal;
					cursor: pointer;
					color: white;
				}

				p {
					font-size: 12px;
					color: red;
					text-align: center;
				}

				span {
					font-size: 12px;
					text-align: center;
				}
			}
		}

		// [ ]: NAVBAR

		.navbar {
			.container {
				padding: 10px 0px;
				display: flex;
				align-items: center;
				justify-content: space-between;
				.logo {
					img {
						width: 120px;
					}
				}
				.links {
					display: flex;
					align-items: center;
					gap: 10px;

					h6 {
						font-size: 16px;
						font-weight: 300;
					}

					span {
						cursor: pointer;
					}

					.write {
						background-color: $lightGreen;
						width: 50px;
						height: 50px;
						-webkit-border-radius: 50%;
						-moz-border-radius: 50%;
						-ms-border-radius: 50%;
						-o-border-radius: 50%;
						border-radius: 50%;
						display: flex;
						align-items: center;
						justify-content: center;
						font-weight: 300;
						border: 1px solid white; // This border is to prevent movement when hover is active and it's border is on.

						&:hover {
							color: teal;
							background-color: white;
							border: 1px solid teal;
							-webkit-transition: all 0.3s ease-in-out;
							-moz-transition: all 0.3s ease-in-out;
							-ms-transition: all 0.3s ease-in-out;
							-o-transition: all 0.3s ease-in-out;
							transition: all 0.3s ease-in-out;
						}
					}
				}
			}
		}

		// [ ]: FOOTER

		footer {
			margin-top: 100px;
			padding: 20px;
			background-color: $lightGreen;
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-size: 12px;
			img {
				width: 50px;
			}
		}
	}
}
